    </div>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="">>> </span>
      </div>
      <div id="official-robot-user-count">
      </div>

      <el-table :data="tableItems" border style="width: 100%;">
        <el-table-column prop="dates" label="" width="80"></el-table-column>
        <el-table-column prop="user.subscribe_members" label=""></el-table-column>
        <el-table-column prop="user.subscribe_owners" label=""></el-table-column>
      </el-table>

    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="">>> </span>
      </div>
      <div id="official-robot-group-managed">
      </div>

      <el-table :data="tableItems" border style="width: 100%">
        <el-table-column prop="dates" label="" width="80"></el-table-column>
        <el-table-column prop="group.total_groups" label=""></el-table-column>
        <el-table-column prop="group.active_groups" label="10"></el-table-column>
        <el-table-column prop="group.subscribe_groups" label=""></el-table-column>
        <el-table-column prop="group.subscribe_owners" label=""></el-table-column>
      </el-table>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="">>> </span>
      </div>
      <div id="official-robot-members-count">
      </div>

      <el-table :data="tableItems" border style="width: 100%">
        <el-table-column prop="dates" label="" width="80"></el-table-column>
        <el-table-column prop="members.total_members" label=""></el-table-column>
        <el-table-column prop="members.manage_members" label=""></el-table-column>
      </el-table>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="">>> </span>
      </div>
      <div style="margin-bottom: 1rem;">
        <span></span>
        <el-select @change="changeRobot" v-model="robotSelected" placeholder="">
          <el-option
            v-for="item in robotsData"
            :label="item.petName"
            :value="item.value">
          </el-option>
        </el-select>
      </div>

      <div id="official-robot-single-detail">
      </div>

