        <el-table-column prop="dates" label="" width="80"></el-table-column>
        <el-table-column prop="total_groups" label=""></el-table-column>
        <el-table-column prop="subscribe_groups" label=""></el-table-column>
        <el-table-column prop="subscribe_owners" label=""></el-table-column>
        <el-table-column prop="active_groups" label="10"></el-table-column>
        <el-table-column prop="bubble_groups" label="110"></el-table-column>
        <el-table-column prop="dead_groups" label=""></el-table-column>
      </el-table>
    </el-card>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="">>> {{dateSingle}}</span>
      </div>

      <el-table :data="robotsData" border style="width: 100%">
        <el-table-column prop="petName" label=""></el-table-column>
        <el-table-column prop="total_groups_single" label=""></el-table-column>
        <el-table-column prop="subscribe_groups_single" label=""></el-table-column>
        <el-table-column prop="subscribe_owners_single" label=""></el-table-column>
        <el-table-column prop="active_groups_single" label="10"></el-table-column>
        <el-table-column prop="bubble_groups_single" label="110"></el-table-column>
        <el-table-column prop="dead_groups_single" label=""></el-table-column>
      </el-table>
    </el-card>

  </div>

</template>

<script>
  import OfficalrobotJs from './Officalrobot.js';
  module.exports = OfficalrobotJs;
</script>



