<template>
  <div class="">
    <div class="block statistics-date-picker-container">
      <span class="demonstration"></span>
      <el-date-picker
        v-model="dateSelected"
        type="daterange"
        align="right"
        placeholder=""
        @change="changeDate"
        :picker-options="pickerOptions">
      </el-date-picker>
    </div>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="">>> </span>
      </div>
      <div id="personal-robot-user-count">
      </div>

      <el-table :data="tableItems" border style="width: 100%;">
        <el-table-column prop="dates" label="" width="80"></el-table-column>
        <el-table-column prop="user.login_users" label=""></el-table-column>
        <el-table-column prop="user.online_users" label=""></el-table-column>
      </el-table>

    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="">>> </span>
      </div>
      <div id="personal-robot-group-managed">
      </div>

      <el-table :data="tableItems" border style="width: 100%">
        <el-table-column prop="dates" label="" width="80"></el-table-column>
        <el-table-column prop="group.total_groups" label=""></el-table-column>
        <el-table-column prop="group.manage_groups" label=""></el-table-column>
      </el-table>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="">>> </span>
      </div>
      <div id="personal-robot-members-count">
      </div>

      <el-table :data="tableItems" border style="width: 100%">
        <el-table-column prop="dates" label="" width="80"></el-table-column>
        <el-table-column prop="members.total_members" label=""></el-table-column>
        <el-table-column prop="members.manage_members" label=""></el-table-column>
      </el-table>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="">>> </span>
      </div>
      <div id="personal-robot-robot-detail">
      </div>

      <el-table :data="tableItems" border style="width: 100%">
        <el-table-column prop="dates" label="" width="80"></el-table-column>
        <el-table-column prop="robot.total_robots" label=""></el-table-column>
        <el-table-column prop="robot.login_robots" label=""></el-table-column>
        <el-table-column prop="robot.online_robots" label=""></el-table-column>
        <el-table-column prop="robot.offline_robots" label=""></el-table-column>
      </el-table>
    </el-card>

  </div>

</template>

<script>
  import PersonalrobotJs from './Personalrobot.js';
  module.exports = PersonalrobotJs;
</script>



