<template>
  <div class="">
    <div class="block overview-date-picker-container">
      <span class="demonstration"></span>
      <el-date-picker
        v-model="dateSelected"
        type="daterange"
        align="right"
        placeholder=""
        @change="changeDate"
        :picker-options="pickerOptions">
      </el-date-picker>
    </div>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="">>> </span>
      </div>
      <div style="margin-bottom: 1rem;">
        <span></span>
        <el-select @change="changeItem" v-model="itemSelected" placeholder="">
          <el-option
            v-for="item in featuresData"
            :label="item.name"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div id="feature-single-detail">
      </div>

      <el-table :data="tableItems[itemSelected] ? tableItems[itemSelected].data : []" border style="width: 100%;">
        <el-table-column prop="dates" label="" width="80"></el-table-column>
        <el-table-column prop="rates" label=""></el-table-column>
        <el-table-column prop="usage_nums" label=""></el-table-column>
      </el-table>

    </el-card>

  </div>

</template>

<script>
  import FeaturesJs from './Features.js';
  module.exports = FeaturesJs;
</script>



