        <el-dialog size="tiny" :title="dialog.title"
            v-model="dialog.show_pass">
            <el-form style="margin:20px;width:80%;"
                label-width="100px"
                :model="dialog.user_info"
                :rules="dialog.user_info_rules"
                ref='user_info'>
                <el-form-item class='edit-form'
                    label=""
                    prop='email'>
                    <el-input v-model="dialog.user_info.email" disabled placeholder=''></el-input>
                </el-form-item>
                <el-form-item class='edit-form'
                    label=""
                    prop='username'>
                    <el-input v-model="dialog.user_info.username" disabled placeholder=''></el-input>
                </el-form-item>
                <el-form-item class='edit-form'
                    label=""
                    prop='old_password'>
                    <el-input
                        type='password'
                        placeholder=''
                        auto-complete='off'
                        v-model="dialog.user_info.old_password"></el-input>
                </el-form-item>
                <el-form-item class='edit-form'
                    label=""
                    prop='password'>
                    <el-input
                        type='password'
                        placeholder=''
                        auto-complete='off'
                        v-model="dialog.user_info.password"></el-input>
                </el-form-item>
                <el-form-item class='edit-form'
                    label=""
                    prop='password_confirm'>
                    <el-input
                        type='password'
                        placeholder=''
                        auto-complete='off'
                        v-model="dialog.user_info.password_confirm"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialog.show_pass = false"> </el-button>
                <el-button type="primary" @click="updUserPass('user_info')"> </el-button>
            </span>
        </el-dialog>


        <el-dialog size="small" :title="dialog.title"
            v-model="dialog.show_set">
            <el-form style="margin:20px;width:80%;"
                label-width="100px"
                v-model='dialog.set_info'
                ref='set_info'>
                <el-form-item label="">
                    <el-select placeholder=""
                        v-model='dialog.set_info.login_style'>
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select placeholder=""
                        multiple
                        v-model='dialog.set_info.disabled_update_pass'>
                        <!-- valueIDnumberdisabled_update_pass
                            value
                            elementUI===
                        -->
                        <el-option
                            v-for='user in dialog.set_info.select_users'
                            :label='user.username'
                            :value='user.id+""'></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialog.show_set = false"></el-button>
                <el-button type="primary" @click="onUpdateSetting"></el-button>
            </span>
        </el-dialog>
    </header>
</template>

<script>
    import HeadNavJs from './HeadNav.js';
    export default HeadNavJs;
</script>

<style scoped lang='less'>
    .logo-container{
        height: 60px;
    }
    .logo{
        height: 50px;
        width: auto;
        margin-left: 10px;
        margin-top: 5px;
      float: left;
    }
    .logo-name {
      display: inline-block;
      margin: 1.2rem 0.5rem;
      position: absolute;
      max-width: 100px;
    }
    .fa-user{
        position: relative;
        top:-2px;
        margin-right: 4px;
    }
    .head-nav{
        width:100%;
        height: 60px;
        background: #324057;
        position: fixed;
        top:0px;
        left:0px;
        z-index: 999;
        color:#FFF;
        border-bottom: 1px solid #1F2D3D;

        .logout{
            width:60px;
            height: 60px;
            line-height: 60px;
            text-align: center;
            cursor: pointer;
          position: absolute;
          top: 0;
          right: 0;
        }
    }
    .userinfo{
        text-align: right;
    }
    .username{
        height: 60px;
        line-height: 60px;
        cursor: pointer;

        .el-dropdown{
            color:#FFF;
        }
    }
    .top_menu_container {
      background: #324057;
      height: 59px;
    }
</style>



